  box-shadow: 0 2px 0 var(--button-side-color), 0 4px 3px var(--shadow);
  transform: translateY(1px);
} */

.controls ul {
  display: grid;
  list-style: none;
  grid-auto-flow: column;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  margin: 0;
  padding: 10px;
  padding-bottom: 20px;
}

.controls li button {
  width: 100%;
  height: 100%;
}

